.home-container {
    padding: 40px;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    background-color: #f9f9f9;
    min-height: 100vh;
  }
  
  .home-header {
    text-align: center;
    margin-bottom: 40px;
  }
  
  .home-header h1 {
    color: #c8102e;
  }
  
  .home-options {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(180px, 1fr));
    gap: 20px;
    max-width: 800px;
    margin: 0 auto;
  }
  
  .option-card {
    background-color: white;
    padding: 30px;
    border-radius: 10px;
    text-align: center;
    font-size: 18px;
    font-weight: bold;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    cursor: pointer;
    transition: transform 0.2s ease, background-color 0.3s ease;
  }
  
  .option-card:hover {
    transform: translateY(-4px);
    background-color: #ffe5e5;
  }
  